/*Elements*/
h1{
  font-weight: 700;
  font-size: 6rem;
  margin: 0;
}

h2{
  font-weight: 500;
  font-size: 4rem;
}

h3{
  font-weight: 300;
  font-size: 2rem;
}

p{
  font-family: 'Roboto', sans-serif;
  font-size: 1.1rem;
}

span{
  font-family: 'Roboto', sans-serif;
  font-size: 1.1rem;
}

hr{
  width: 75%;
  height: 0;
  border-width: 5px;
  border-style: none;
  border-top-style: dotted;
  border-color: #220c10;
}

a{
  color: #007eff;
  text-decoration: none;
  font-weight: bold;
}

section{
  padding: 5% 0;
}

section h1{
  margin: 0 auto 5%;
}

section#portfolio{
  padding: 5% 0 0;
}

/*Classes*/
.App {
  text-align: center;
  background-color: #007eff;
  font-family: 'Signika', sans-serif;
  color: #fff9fb;
}

.paperBG{
  background-color: #fff9fb;
  color: #220c10;
}

.darkRedBG{
  background-color: #220c10;
}

.murkyBG{
  background-color: #0d1b1a;
}

.hrLight{
  border-color: #fff9fb;
}

.headerImg{
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 0;
  contain: content;
  object-fit: fill;
}

.placeholderImg{
  background-color: #0d1b1a;
  contain: content;
  height: 100%;
  margin: 0;
}

.introInfo{
  position: relative;
  z-index: 1;
}

.borderText{
  width: 50%;
  background: rgb(0,0,0);
  background: linear-gradient(90deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.50) 20%, rgba(0,0,0,0.50) 80%, rgba(0,0,0,0) 100%); 
}

h3.borderText{
  margin: 4% auto 0;
}

.infoCard{
  padding: 3% 5%;
}

.infoCard p{
  text-align: center;
}

.skillTitle{
  padding: 3%;
  transition: all .2s linear 0.1s;
}

.skillTitle:hover{
  cursor: pointer;
}

.gridListItem:hover{
  cursor: zoom-in;
}

.showcaseDisplay{
  transition: all .2s linear 0.2s;
}

.showcaseDisplay.deployed{
  transition: all .2s linear;
}

.cardDetails{
  margin: 5%;
}

.softIcon{
  margin: 0 5px;
}

.cardDetails h3{
  margin: 3% auto 2%;
}

.emptySection{
  padding: 5% 0 50%;
}

.whiteIcon .st0{
  fill: #fff9fb;
}

.darkIcon .st0{
  fill: #220c10;
}

.footer{
  font-size: 1rem;
  padding: 1%;
  margin: 0;
}

.card{
  border-radius: 15px;
  height: 100%; 
  padding: 2% 10% 4%;
}

.card h3{
  margin: 10px auto 0;
}

.card strong{
  color: #007eff;
}

.clickableCard:hover{
  background-color: #007eff;
  color: #fff9fb;
  cursor: pointer;
}

.clickableCard:hover hr{
  border-color: #fff9fb;
}

/*IDs*/
#webIcon{
  border-radius: 100%;
  border: solid #220c10 0.2rem;
  height: 50px;
  padding: 0;
  margin: 5px 0 0;
}

#introAvatar{
  border-radius: 100%;
  border: solid #220c10 0.2rem;
  height: 200px;
  padding: 0;
  margin: 0;
}

#about{
  padding: 5% 0 0;
}

#portfolio{
  background-color: #ba3f1d;
}

#renderCanvas {
  width: 100%;
  height: 100%;
  display: block;
  font-size: 0;
}

#contact h1{
  margin: 0;
}

#contact p{
  margin: 0 auto 5%;
}